.tab {
  display: flex;
  div {
    display: flex;
    align-items: center;
    justify-content: center;
    color: #80bd01;
    margin: 8px;
    padding: 3px 4px;
    font-size: 14px;
    cursor: pointer;
    &.active {
      background: #80bd01;
      color: #ffffff;
      border-radius: 2px;
    }
  }
}
.content {
  height: 50px;
  border: 1px solid #eee;
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  > span{
    margin: 0 4px;
    .reply {
      font-size: 14px;
      color: #9e78c0;
    }
    .visit {
      font-size: 10px;
      color: #b4b4b4;
    }
  }
  > .information {
    position: relative;
    cursor: pointer;
    &:hover::after{
      position: absolute;
      content: '';
      display: block;
      bottom: 0;
      left: 0;
      right: 0;
      border-bottom: 1px solid black;
    }
  }
  .avatar {
    width: 30px;
    img{
      max-width: 100%;
      border-radius: 4px;
    }
  }
  .title {
    border-radius: 2px;
    padding: 2px 4px;
    font-size: 12px;
  }
  .top {
    background: #80bd01;
    color: #ffffff;
  }
  .no-top {
    color: #aaa;
    background: #ccc;
  }
  .relative-time {
    margin-left: auto;
  }

}
